<template>
    <div class="bs-catalog">
        <div class="bs-catalog-content">
            <ul 
                v-for='booksitem in list'
                :key="booksitem.id"
                >
                <li>
                    <i 
                    class="el-icon-edit"
                    @click="toggleTitle(booksitem.id,booksitem.type_name)"
                    v-show="booksitem.type_id == lables ? false : true"
                    ></i>
                    <span v-show="booksitem.type_id == lables ? false : true">{{booksitem.type_name}}</span>
                    <input 
                        type="text" 
                        v-show="booksitem.type_id == lables ? true : false"
                        v-model="inputTitle"
                    >
                    <i 
                        class="el-icon-check"
                        v-show="booksitem.type_id == lables ? true : false"
                        @click="modifyBookTitle(booksitem.id,booksitem.type_describe)"
                    ></i>
                </li>

                <li 
                    v-for="item in booksitem['all_book']"
                    :key="item.default_doc"
                >
                    <i 
                        class="el-icon-edit"
                        @click="toggleTag(item.book_id,item.book_name)"
                        v-show="item.book_id == lable ? false : true"
                    ></i>
                    <span v-show="item.book_id == lable ? false : true">{{item.book_name}}</span>
                    <input 
                        type="text" 
                        v-show="item.book_id == lable ? true : false"
                        v-model="inputTitle"
                    >
                    <i 
                        class="el-icon-check"
                        v-show="item.book_id == lable ? true : false"
                        @click="modifyBook(item.id,booksitem.id,item.book_describe)"
                    ></i>
                </li>
            </ul>
            
        </div>
        <addCatalog :list='list' @modifysuccess='modifysuccess'></AddCatalog>
    </div>
</template>

<script>
import * as ajax from "@/api";
import addCatalog from '@/components/Catalog/addCatalog'

export default {
    data(){
        return{
            list:[],
            lable:'',
            lables:'',
            inputTitle:''
        }
    },
    created(){
        ajax.getOutlineList().then(resp=>{
            if(resp.data.code){
                this.list = resp.data.data.article_category
            }
        })
    },
    methods:{
        toggleTag(id,name){
            this.lable = id
            this.inputTitle = name
        },
        modifyBook(second_id,one_id,describe){
            ajax.modifySecondTitle(second_id,this.inputTitle,describe,one_id).then(resp=>{
                if(resp.data.code){
                    this.inputTitle = ''
                    this.lable = ''
                    this.list = resp.data.data.article_category
                    alert('修改成功')
                }
            })
        },
        toggleTitle(id,name){
            this.lables = id
            this.inputTitle = name
        },
        modifyBookTitle(id,describe){
            ajax.modifyArticleType(id,this.inputTitle,describe).then(resp=>{
                if(resp.data.code){
                    this.inputTitle = ''
                    this.lables = ''
                    this.list = resp.data.data.article_category
                    alert('修改成功')
                }
            })
        },
        modifysuccess(data){
            this.list = data
        }
    },
    components:{
        addCatalog
    }
}
</script>

<style lang='scss'>
    
   .bs-catalog{
       flex: 1;
       padding: 30px;
       position: relative;
       min-width: 1000px;
       &-content{
           ul{
               list-style: none;
               margin-bottom: 50px;
               
               li{
                   border-bottom: 1px solid #888;
                   height: 50px;
                   line-height: 50px;
                   padding-left: 10px;
                   
                   .el-icon-edit{
                       margin-right: 30px;
                       font-size: 20px;
                       display: none;
                   }
                   .el-icon-check{
                       color: rgb(35, 107, 175);
                       font-size: 30px;
                   }
                   input{
                       width: 200px;
                       height: 35px;
                       margin-right: 20px;
                   }
               }
               li:hover{
                   color: red;
                   .el-icon-edit{
                       display: inline-block;
                   }
               }
               li:nth-child(1){
                   background-color: #999;
                   color: #fff;
               }
           }
       }
   }
</style>